*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
}


@media only screen and (min-width: 700px){
  .footer-group-left{  
    display: flex;
    justify-content: space-between;
    gap: 1rem;
  }
  .footer-group-right{  
    display: flex;
    justify-content: space-between;
    gap: 1rem;
  }
  .footer-bottom {
    display: flex;
    justify-content: space-between;
  }
}

@media only screen and (max-width: 700px){
  .footer-bottom {
    display: grid;
    justify-content: space-between;
  }
  .footer-group-left{
    display: flex;
    justify-content: space-between;
    gap: 1rem;
  }
  .footer-group-right{
    display: flex;
    justify-content: space-between;
    gap: 1rem;
    margin: 0 auto;
  }
}

body{
  width: 100%;
  min-height: 100vh;
}


.navbar{
  width: 100%;
  height: 65px;
  position: relative;
}

.navbar-content{
  display: flex;
  position: absolute;
  right: 0;
  padding-right: 1rem;
}

.navigation{
  display: flex;
  justify-content: space-between;
  gap: 2rem;
}

.navigation ul{
  display: flex;
  gap: 1rem;
  list-style-type: none;
}

.navigation ul{
  margin: 0;
  height: 100%;
}

li a{
  color: #111;
  text-decoration: none;
  font-size: 13px;
}

li a:hover{
  text-decoration: underline;
  color: #111;
}

.navigation img{
  border-radius: 2rem;
}




img{
  max-width: 100%;
}






/* main */

.main-content{
  display: grid;
  text-align: center;
}

.main{
  min-height: 66vh;
}

.search-bar{
  display: flex;
  z-index: 3;
  height: 44px;
  background: #fff;
  border: 1px solid #dfe1e5;
  box-shadow: none;
  border-radius: 24px;
  width: calc(100% - 40px);
  max-width: 570px;
  margin: 0 auto;
  margin-top: 1.4rem;
}

.search-bar:hover{
  box-shadow: rgba(70, 70, 70, 0.12) 0px 1px 8px, rgba(88, 88, 88, 0.24) 0px 1px 2px;
}

.search-bar input{
  width: 100%;
  border: none;
  padding-left: .5rem;
}

.search-bar input:focus{
  outline: none;
}

.search-bar i{
  padding: .7rem .9rem 0 .9rem;
  color: #949496;
}

.content-footer{
  padding: 1.4rem 0;
}

.content-footer input {
  padding: .5rem 1.2rem;
  margin: .5rem;
  font-size: 14px;
  background: none;
  border: none;
  border-radius: 2px;
}

.content-footer input:hover{
  box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px, rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
}

.footer-language{
  font-size: 13px;
  display: flex;
  gap: 1rem;
  justify-content: space-between;
  margin: 0 auto;
}

.footer-language a{
  color: #1a0dab;
  text-decoration: none;
}

.footer-language a:hover{
  color: #1a0dab;
  text-decoration: underline;
}







/* Footer */

.footer-content{
  position: absolute;
  bottom: 0;
  background: #f2f2f2;
  color: #70757a;
  font-size: 15px;
  width: 100%;
}

.country{
  border-bottom: 1px solid rgb(218, 218, 218);
  padding: .9em;

}

.footer-bottom a{
  color: #70757a;
  text-decoration: none;
}

.footer-bottom a:hover{
  text-decoration: underline;
  color: #70757a;
}

.footer-bottom {
  padding: 1rem 2rem 1rem 2rem;
}

